@import '../../styles/partials/settings';

.about {
  text-align: center;
  position: relative;
  .about-bg {
    //background-image: url('../../assets/images/global/IMG_20190708_233931__01.jpg');
    background-image: url('../../assets/images/global/IMG_20190708_233942.jpg');
    opacity: 0.2;
  }
  .inner-wrapper {
    max-width: 1200px;
    padding: 20px;
    margin: auto;
    min-height: 520px;
    @media(min-width: $tablet-width) {
      min-height: 800px;
      padding-top: 150px;
    }
    @media(min-width: $small-desktop-width) {
      min-height: 94vh;
    }
    .about-me, .skills {
      text-align: left;
      width: 100%;
      max-width: 500px;
      display: inline-block;
      @media(min-width: $tablet-width) {
        width: 49%;
      }
      h1 {
        margin-top: 40px;
        text-align: center;
      }
      &.about-me {
        @media(min-width: $tablet-width) {
          float: left;
        }
      }
      &.skills {
        @media(min-width: $tablet-width) {
          float: right;
        }
        .skills-list {
          padding: 0;
          .slideIn {
            display: inline-block;
            animation: slideIn 3s forwards;
            transform: translate3d(4000 + px, 0, 100px);
            padding: 12px;
            border: 1px solid rgba(255,255,255,0.3);
            @for $i from 1 through 33 {
              &:nth-child(#{$i}n) {
                animation-delay: #{$i * 0.1}s;
              }
            }
          }
        }
      }
    }
  }
}

@keyframes slideIn {
  0% {
    opacity: 0;
    background: blue;
    transform: translate3d(4000 + px, 0, 100px);
  }
  99% {
    background: red;
  }
  100% {
    opacity: 1;
    background: black;
    transform: translate3d(0, 0, 0);
  }
}